import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'values'
})
export class ValuesPipe implements PipeTransform {

  transform(value: any, args: any[] = null): any {
    if(value) {
      return Object.keys(value).map(key => value[key]);
    }
    return [];
  }

}



